.navbar {
  background-color: transparent !important;
  position: absolute !important;
  top: 0;
  width: 100%;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.navbar-container {
  display: flex !important;
  flex-direction: row !important;
  align-items: center;
  justify-content: space-between;
}

.navbar-right span {
  cursor: pointer;
  padding: 24px 20px;
  font-size: 13px;
  font-family: "Poppins", Arial, sans-serif;
  color: rgba(255, 255, 255, 0.9);
  letter-spacing: 2px;
  line-height: 23.4px;
  text-transform: uppercase;
  transition: 0.2s ease-in-out;
}

.navbar-right span:hover {
  color: #c49b63;
}

.hamburger-menu span {
  cursor: pointer;
  padding: 20px 0px 20px 10px;
  font-size: 16px;
  font-family: "Poppins", Arial, sans-serif;
  color: rgba(255, 255, 255, 0.9);
  letter-spacing: 2px;
  line-height: 23.4px;
  text-transform: uppercase;
  transition: 0.2s ease-in-out;
}

.hamburger-menu {
  display: none;
}

.burger g {
  fill: white;
}

.navbar-left {
  text-align: center;
}

.navbar-left p {
  cursor: pointer;
  color: white;
  font-family: 'Josefin Sans', sans-serif;
  font-size: 22px;
  line-height: 22px;
  padding-left: 20px;
  margin-top: 7px;
  text-transform: uppercase;
}

.navbar-left span {
  display: block;
  font-size: 12px;
  letter-spacing: 5px;
  line-height: 12px;
  padding: 5px 0;
  text-transform: uppercase;
}

.active-nav {
  color: #c49b63 !important;
}

.second-navbar {
  width: 100%;
  left: 0;
  background-color: #151111;
}

.second-navbar .navbar {
  max-width: 99.9vw;
  position: fixed !important;
  background-color: #151111 !important;
  top: -100px !important;
  border: 0;
  transition: all 0.4s !important;
}

.second-navbar .sticky-navbar {
  position: fixed !important;
  top: 0 !important;
  transition: top 0.4s !important;
}

.scroll-burger-nav {
  max-height: 0;
  height: auto;
  background-color: #151111;
  overflow: hidden;
  transition: all 0.5s;
}

.scrolled-burger {
  max-height: 655px;
  transition: all 0.5s;
}

.burger-nav-span {
  display: block;
  cursor: pointer;
  padding: 24px 20px;
  font-size: 13px;
  font-family: "Poppins", Arial, sans-serif;
  color: rgba(255, 255, 255, 0.9);
  letter-spacing: 2px;
  line-height: 23.4px;
  text-transform: uppercase;
  transition: 0.2s ease-in-out;
}

.burger-nav-span:first-child {
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}

@media only screen and (max-width: 768px) {
  .navbar {
    background-color: #151111 !important;
    position: absolute !important;
    top: 0;
    width: 100%;
    border-bottom: 0;
  }

  .hamburger-menu {
    display: flex;
  }

  .navbar-right {
    display: none;
  }

  .navbar-container {
    padding: 0 20px !important;
  }

  .navbar-left p {
    padding-left: 0;
  }

  .second-navbar .navbar {
    display: none;
  }

  .navbar {
    max-width: 99.9vw;
    position: fixed !important;
    background-color: #151111 !important;
    top: 0px !important;
    border: 0;
    transition: all 0.4s !important;
  }
}


.carousel-background {
  height: 750px;
}

.carousel-content-container {
  z-index: 5;
  display: flex;
  flex-direction: column;

}

.carousel ul {
  padding-bottom: 75px;
  margin: 0 auto;
}

.slick-dots li button::before {
  color: rgba(255, 255, 255, 0.7);
  border: 2px solid rgba(255, 255, 255, 0.7);
  border-radius: 50%;
  font-size: 16.4px;
  height: 22px;
  width: 22.3px;
  opacity: 1;
  transition: 0.3s ease-in-out;
}

.slick-dots li button:hover::before {
  color: rgba(255, 255, 255, 1);
  border: 2px solid rgba(255, 255, 255, 1);
}

.slick-dots li.slick-active button:before {
  color: rgba(255, 255, 255, 1);
  border: 2px solid rgba(255, 255, 255, 1);
  opacity: 1;
}

.carousel-overlay {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 750px;
  background-color: black;
  opacity: 0.3;
  z-index: 1;
}

.carousel-img-container {
  padding-top: 119.5px;
  width: 100%;
  height: 100%;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  z-index: 0;
  display: flex;
  flex-direction: column;
}

.carousel-img-container span {
  font-size: 30px;
  margin-bottom: 0;
}

.carousel-img-container h2 {
  margin-bottom: 24px;
  line-height: 1.5;
}

.carousel-img-container p {
  font-size: 18px;
  color: white;
  margin-bottom: 48px;
}

.carousel-btns {
  margin: -80px auto 0 auto;
  z-index: 2;
}

.carousel-btn {
  margin: 0 2.5px;
  font-family: "Poppins", Arial, sans-serif;
  font-size: 13px;
  padding: 16px 24px;
  z-index: 3;
}

.carousel-btn-left {
  color: black;
  border: 1px solid #c49b63;
  background-color: #c49b63;
  transition: 0.2s ease-in-out;
}

.carousel-btn-left:hover {
  background-color: transparent;
  color: #c49b63;
}

.carousel-btn-right {
  color: white;
  border: 1px solid white;
  background-color: transparent;
  transition: 0.2s ease-in-out;
}

.carousel-btn-right:hover {
  background-color: #c49b63;
  border: 1px solid #c49b63;
  color: black;
}

@media only screen and (max-width: 768px) {
  .carousel-btns {
    margin: -40px auto 0 auto;
  }
}

@media only screen and (max-width: 478px) {
  .carousel-background .component-title {
    font-size: 32px !important;
  }
}


